.disable {
  background-color: lightgray !important;
  color: black !important; }

@-webkit-keyframes wiggle {
  0% {
    -webkit-transform: rotate(4deg); }
  50% {
    -webkit-transform: rotate(-4deg); }
  100% {
    -webkit-transform: rotate(4deg); } }

@media only screen and (min-width: 40.063em) {
  .wiggle:hover {
    -webkit-animation: wiggle 1.5s infinite; } }
