.PortText {
    width: 100%;
    margin-bottom: 3em;

    h1 {
      font-size: 4em;
      color: #111;
      margin: .3em 0;
      margin-top: .5em;
      line-height: 1em;
      font-family: "ff-prater-sans-web";    
    }

    p {
        color: #888;
        border: 1px solid #e2e2e2;
        padding: 1em;
        background-color: white;
        font-family: 'Open-Sans';
        font-size: 1.1em;
        line-height: 1.8em;
    }

    span {
        color: #222;
        font-size: 1.8em;
        display: block;
        font-family: 'league-Gothic';
        margin-bottom: 10px;
    }

    
    @media only screen and (min-width: 40.063em) { 
        margin-bottom: 0;
        h1 {
            font-size: 4em;
        }
    }

    @media only screen and (min-width: 64.063em) {
        width: 50%;
        display: inline-block;

        h1 {
           margin-top: 0.8em;
           font-size: 4em;
        }


    }

    @media only screen and (min-width: 90.063em) { 

    }
}

