.PortContent {

    @media only screen and (min-width: 40.063em) { 
        width: 70%;
        margin: 0 auto;
        height: 300px;
        position: relative;

        h1 {
            display: none;
        }
    }

    @media only screen and (min-width: 64.063em) {
        width: 48%;
        display: inline-block;
        height: 300px;
        position: relative;
        left: 2em;
        padding-left: 2%;
        vertical-align: top;
        margin-top: 2em;
    }

    @media only screen and (min-width: 90.063em) { 
        width: 40%;
        display: inline-block;
    }
}

