@import url(https://fonts.googleapis.com/css?family=Roboto);


@font-face {
  font-family: 'pratersanstwo';
  font-weight: 600;
  src: url('/content/fonts/pratersanstwo.eot'); /* IE9 Compat Modes */
  src: url('/content/fonts/pratersanstwo.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('/content/fonts/pratersanstwo.woff2') format('woff2'), /* Super Modern Browsers */
       url('/content/fonts/pratersanstwo.woff') format('woff'), /* Pretty Modern Browsers */
       url('/content/fonts/pratersanstwo.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('/content/fonts/pratersanstwo.svg#svgFontName') format('svg'); /* Legacy iOS */
}



